:root {
    --col-main-blue: #01a6e8;
    --col-main-blue-hover: #0993ca;
    --col-main-dark-blue: #054c68;
    --col-main-orange: #ffac14;
    --col-main-orange-hover: #ec9800;
    --col-main-orange-hover: #9d6600;
    --col-white: #edeaea;
    --col-black: #111111;
    --col-dark-theme: #1a1a1a;
    --col-light-grey: #d3d3d3;
    --col-dark-grey: #aaa9a9;
    --col-light-theme-main: #f4f4f6;
    --col-light-theme-alt: #f3f3f7;
}

.container {
    width: min(100% - 30px, 1280px);
    margin-inline: auto;
    min-height: 100vh;
}

.flex {
    display: flex;
}

.center {
    height: 100%;
    display: grid;
    place-items: center;
    justify-content: center;
    align-items: center;
}

body {
    background-color: var(--col-dark-theme);
}

.reviewBox {
    width: 100%;
}

.stars {
    display: flex;
    width: 100%;
    height: 175px;
    gap: 15px;
}

h1, h2, h3, p {
    color: #fff;
}

h2 {
    font-size: 35px;
}

h1 {
    font-size: 90px;
    cursor: pointer;
}

h3 {
    font-size: 30px;
    font-weight: 400;
}

.starSelect {
    color: #383838;
    cursor: pointer; 
}

.starSelect.Selected {
    color: #ffd700;
    cursor: pointer; 
}

.dialogBox {
    width: 550px;
    height: 300px;
    background-color: #383838;
    border-radius: 5px;
    position: fixed;
}

.openBtn {
    position: fixed;
    right: 60px;
    bottom: 60px;
    width: 120px;
    height: 42px;
    border-radius: 5px;
    background-color: var(--col-main-blue);
    color: #fff;
}

.openBtn:hover {
    background-color: var(--col-main-blue-hover);
    transition: all 0.3s ease-in-out;
}

.closeBtn {
    width: 90px;
    height: 35px;
    font-size: 20px;
    border-radius: 5px;
    background-color: var(--col-main-orange);
    color: #fff;
}

.closeBtn:hover {
    background-color: var(--col-main-orange-hover);
    transition: all 0.3s ease-in-out;
}

.content {
    height: 120px;
}